/***** General *****/
.flex, .flex-column {
  display: -ms-flexbox;
  display: flex;
}

.flex.center-all {
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
}

/***** Flex Row *****/
.flex.spread {
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.flex.end {
  -ms-flex-pack: end;
      justify-content: flex-end;
}

/***** Flex Column *****/
.flex-column {
  -ms-flex-direction: column;
      flex-direction: column;
}

.column-center {
  -ms-flex-line-pack: center;
      align-content: center;
}

.column-end {
  -ms-flex-line-pack: end;
      align-content: flex-end;
}

/***** Flex Items *****/
.flex-item-1 {
  -ms-flex: 1 1;
      flex: 1 1;
}

.flex-item-2 {
  -ms-flex: 2 1;
      flex: 2 1;
}

.flex-item-3 {
  -ms-flex: 3 1;
      flex: 3 1;
}

.flex-item-4 {
  -ms-flex: 4 1;
      flex: 4 1;
}

.flex-item-5 {
  -ms-flex: 5 1;
      flex: 5 1;
}


